@import "./reset.scss";

@import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');

:root {
    --base-color: #1a1a1a;
    --invert-color: #fafafa;
    --border-color: #c4c4c4;
    --color-1: #b2954c;
    --color-2: #89bf54;

    --grid-border-color-ext: #b0a986;
    --grid-border-color-int: #ffffff;
}

body {
    padding: 0;
    font-size: 14px;
    font-family: 'Roboto', sans-serif;
    line-height: 1.333;
}

html, body, .root, .page {
    position: relative;
    height: 100%;
}
.wrap {
    position: relative;
    min-height: 100%;
    padding: 60px 0 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.main-container {
    width  : 100%;
    padding: 50px 0; 
    margin-left: auto;
    margin-right: auto;   
}

@media(min-width: 768px) {
    .main-container {
        max-width: 720px;
    }
}

@media(min-width: 1024px) {
    .main-container {
        max-width: 960px;
    }
}

@media(min-width: 1200px) {
    .main-container {
        max-width: 1140px;
    }
}