.header {
    height    : 60px;
    position  : fixed;
    z-index   : 1;
    top       : 0;
    left      : 0;
    right     : 0;
    padding   : 10px;
    box-shadow: 0px 1px 7px 3px rgba(0, 0, 0, 0.3);
    background: #ffffff;

    &-logo {
        display: inline-block;

        &-link {
            display        : block;
            text-decoration: none;
            color          : var(--base-color);
        }

        &-figure {
            display       : flex;
            flex-direction: row;
            align-items   : center;
        }

        &-img {
            margin-right: 10px;
        }

        &-name {
            color    : inherit;
            font-size: 20px;
        }
    }

    &-smo {

        &:before,
        &:after {
            content: "";
            display: table;
            clear  : both;
        }

        &-ul {
            display       : flex;
            flex-direction: row;
            white-space   : nowrap;
            align-items   : center;
            height        : 100%;
        }

        &-li {
            display: inline-block;
        }

        &-a {
            display        : flex;
            width          : 32px;
            height         : 32px;
            align-items    : center;
            justify-content: center;
        }

        &-img {
            display: block;
            margin : 0;
            width  : 20px;
            height : 20px;
        }

        +.header-nav-toggle {
            margin-left: 20px;
        }
    }

    &-nav {
        display: flex;
        float  : right;
        height : 100%;

        &-toggle {
            display         : flex;
            width           : 40px;
            height          : 40px;
            align-items     : center;
            justify-content : center;
            border-radius   : 4px;
            border          : 1px solid #a1a1a1;
            background-color: #fff;

            &:hover {
                cursor: pointer;
            }
        }

        &-icon {
            display        : flex;
            width          : 24px;
            height         : 24px;
            flex-direction : column;
            align-items    : center;
            justify-content: center;
        }

        &-line {
            display         : inline-block;
            width           : 17px;
            height          : 3px;
            margin          : 2px 0;
            background-color: var(--base-color);
        }

        &-content {
            position        : absolute;
            top             : 100%;
            left            : 0;
            right           : 0;
            background-color: #fafafa;
            margin-top      : 1px;
            max-height      : 0px;
            overflow        : hidden;
            transition      : max-height 100ms ease-in-out;

            &.drop {
                max-height: 117px;
            }
        }

        &-ul {
            display: block;
        }

        &-li {
            display      : block;
            text-align   : center;
            border-bottom: 1px solid var(--border-color);
        }

        &-a {
            display        : block;
            text-decoration: none;
            color          : var(--color-1);
            padding        : 10px 0;

            &:hover,
            &:active {
                color: var(--color-2);
            }
        }

        &-name {
            display: block;
        }
    }
}

@media(min-width: 768px) {
    .header {
        padding-left : 20px;
        padding-right: 20px;

        &-smo {
            position        : fixed;
            top             : 50%;
            right           : 0;
            background-color: #fcd46d;

            &-ul {
                flex-direction: column;
                padding       : 15px 2px;
            }

            &-img {
                margin: 0 0 0 2px;
            }
        }

        &-nav {
            &-toggle {
                display: none;
            }

            &-content {
                position        : static;
                max-height      : initial;
                margin          : 0;
                background-color: transparent;
            }

            &-ul {
                display       : flex;
                flex-direction: row;
            }

            &-li {
                border-bottom: none;
            }

            &-a {
                margin-left  : 5px;
                margin-right : 5px;
                padding-left : 10px;
                padding-right: 10px;
            }
        }
    }
}

@media(min-width: 1360px) {
    .header {
        padding-left : 50px;
        padding-right: 50px;
    }
}