.page-details {
    flex-grow          : 999;
    background-image   : url("/img/background.webp");
    background-repeat  : no-repeat;
    background-position: center center;
    background-size    : cover;

    &-title {
        margin    : 0 0 15px;
        font-size : 26px;
        text-align: center;
    }

    &-media {
        width    : 516px;
        max-width: 100%;
        margin   : 0 auto;
    }

    &-img {
        width    : 516px;
        max-width: 100%;
        height   : 345px;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
    }

    // page-details-info-a
    &-info {
        &-a {
            display         : block;
            max-width       : 300px;
            margin          : 0 auto;
            background-color: #bada55;
            transition      : background-color 100ms ease-in-out;
            border-radius   : 7px;
            line-height     : 34px;
            text-align      : center;
            text-decoration : none;
            color           : var(--base-color);
            transform       : translate(0, -50%);
        }
    }

    &-navigation {
        display: block;
        margin : 20px 0;

        &:before,
        &:after {
            content: "";
            display: table;
            clear  : both;
        }
    }

    &-back {
        float: left;

        &-link {
            display         : inline-block;
            padding         : 10px 15px;
            border-radius   : 7px;
            background-color: var(--color-1);
            font-style      : italic;
            color           : var(--base-color);
            text-decoration : none;
        }
    }

    &-singup {
        float: right;

        &-link {
            display         : inline-block;
            padding         : 10px 15px;
            border-radius   : 7px;
            background-color: var(--color-2);
            box-shadow      : 0 0 10px #ffffff;
            font-style      : italic;
            color           : var(--base-color);
            text-decoration : none;

            &-name {
                &:before {
                    content: attr(data-full);
                }
            }
        }
    }

    &-content {
        padding         : 15px 25px;
        background-color: rgba(255, 255, 255, 0.8);
        font-size       : 16px;

        p {
            margin-bottom: 15px;
        }

        ul {
            display            : block;
            list-style-type    : disc;
            list-style-position: inside;

            li {
                display: list-item;
            }
        }

        h1, h2, h3, h4, h5, h6 {
            margin-bottom: 10px;
        }
        h1, h2, h3, h4, h5, h6, b {
            font-weight: bold;
        }
    }
}