.our-services {
    width  : 100%;
    padding: 50px 0;

    &-title {
        color      : var(--base-color);
        text-shadow: rgba(0, 0, 0, 0.4) 0px 4px 5px;
        font-size  : 32px;
        text-align : center;
    }

    .grid-shadow {
        position         : absolute;
        bottom           : -26px;
        width            : 165px;
        height           : 26px;
        background-image : url("/img/grid-shadow.png");
        background-repeat: no-repeat;
    }

    &-grid {

        &-container {
            padding-bottom: 20px;
            border-bottom : 1px solid #c4c4c4;
        }

        &-item {
            margin-top: 25px;

            // &:last-child{
            //     .our-services-grid-container {
            //         padding-bottom: 0;
            //         border-bottom : none;
            //     }
            // }
        }

        &-media {
            display            : block;
            width              : 100%;
            height             : 480px;
            background-position: 50% 50%;
            background-repeat  : no-repeat;
            background-size    : cover;
            // border          : 2px solid var(--grid-border-color-int);
        }

        &-title {
            &-h3 {
                display       : block;
                margin-top    : 15px;
                font-size     : 18px;
                text-align    : center;
                text-transform: uppercase;
                color         : var(--base-color);
            }

            &-a {
                text-decoration: none;
                color          : inherit;
                font-weight    : bold;
            }
        }

        &-info {
            margin-top: 15px;

            &-a {
                display         : block;
                max-width       : 300px;
                margin          : 0 auto;
                background-color: #bada55;
                transition      : background-color 100ms ease-in-out;
                border-radius   : 7px;
                line-height     : 34px;
                text-align      : center;
                text-decoration : none;
                color           : var(--base-color);

                &:hover {
                    background-color: #ffd45a;
                }
            }
        }

        &-desc {
            display    : block;
            max-width  : 300px;
            margin     : 15px auto 0;
            font-weight: bold;
            text-align : center;
        }

        &-more {
            display   : block;
            max-width : 300px;
            margin    : 15px auto 0;
            text-align: center;
            font-size : 16px;
            color     : var(--base-color);

            &-a {
                text-decoration: none;
                color          : #2058d3;
            }
        }
    }
}

@media(max-width: 767px) {
    .our-services {
        &-grid {
            &-item {

                &:last-child {
                    .our-services-grid-container {
                        padding-bottom: 0;
                        border-bottom : none;
                    }
                }
            }
        }
    }
}

@media(min-width: 768px) {
    .our-services {
        max-width: 720px;
        margin   : 0 auto;
        padding  : 50px 0;

        &-title {
            font-size: 46px;
        }

        &-grid {
            display              : grid;
            grid-template-columns: 50% 50%;
            margin-left          : -10px;
            margin-right         : -10px;

            &-item {
                width        : 100%;
                margin-top   : 25px;
                padding-left : 10px;
                padding-right: 10px;
            }

            &-container {
                border        : 2px solid var(--grid-border-color-ext);
                padding-bottom: 15px;
                position      : relative;
                height        : 100%;
            }

            &-media {
                border: 2px solid var(--grid-border-color-int);
            }
        }
    }
}

@media(min-width: 1024px) {
    .our-services {
        max-width: 960px;

        &-grid {
            grid-template-columns: 33.3333% 33.3333% 33.3333%;
        }
    }
}

@media(min-width: 1200px) {
    .our-services {
        max-width: 1140px;
    }
}