.footer {
    display        : flex;
    width          : 100%;
    height         : 40px;
    flex-direction : row;
    justify-content: center;
    align-items    : center;
    position       : absolute;
    top            : 100%;
    transform      : translate(0, -100%);
    background-color: var(--color-1);

    &-copy {
        text-align: center;
    }
}